import db from '../Contexts/firebase'
import { collection, getDocs, setDoc, doc, getDoc, updateDoc, Timestamp, query, where } from "firebase/firestore";
import { getStorage, ref, uploadBytes, getDownloadURL } from "firebase/storage";


/*------------------------------------- products--------------------------------------------- */
const getProductsFromFirestore = async ()=>{
    const output = [];
    const data = await getDocs(collection(db, "products"));
    data.forEach((doc) => {
        if(doc.id !== "general"){
            let tmp = doc.data();
            output.push({
                id: doc.id,
                name: tmp.name,
                thumbnail: tmp.thumbnail,
                description: tmp.description,
                price: tmp.price,
                code: tmp.code,
                category: tmp.category,
                show: tmp.show,
                createDate: `${tmp.createDate.toDate().getDate() +"/"+ (tmp.createDate.toDate().getMonth() +1) +"/"+ tmp.createDate.toDate().getFullYear()}`
                });
        }
    });
    return output;
}

export const getProductCaching = async (callback)=>{
    try{
        const req = await fetch('https://caching-system.herokuapp.com/?appID=dinoshop&action=get-public&data=product',{
            mode:"cors",
            method: "POST"
        });
        console.log("get from cache");
        return await req.json();
    } catch (err){
       const req = await getProductsFromFirestore();
       const req1 =  await createProductCaching(req);
       return req;
    }
}

const createProductCaching = async (data)=>{
    const req = await fetch(`https://caching-system.herokuapp.com/?appID=dinoshop&action=create-public&data=product&content=${JSON.stringify(data)}`,{
        mode:"cors",
        method: "POST",
    });
} 

export const getAllProducts = async ()=>{
    const res = await getProductCaching();
    return res;
}

export const createProduct = async(data,mode="w", id=null)=>{
    const _doc = doc(db, "products", "general");
    const query = await getDoc(_doc);
    let productImage = null;
    if (query.exists()) {
        if(mode === "w"){
            if(data.file !== null){
                const storage = getStorage();
                const storageRef = ref(storage, data.file.name);
                const uploadImage = await uploadBytes(storageRef,data.file);
                productImage = await getDownloadURL(ref(storage, data.file.name));
            }
            await setDoc(doc(db, "products",  "p"+(query.data().total + 1 )), {
                name: data.name,
                category: data.category,
                price: data.price,
                code: data.code,
                description: data.description,
                createDate: Timestamp.fromDate(new Date()),
                show: data.status,
                thumbnail: productImage
              });
            await updateDoc(_doc, {total: query.data().total + 1 }); 
        }else{
            if(data.file !== null){
                const storage = getStorage();
                const storageRef = ref(storage, data.file.name);
                const uploadImage = await uploadBytes(storageRef,data.file);
                productImage = await getDownloadURL(ref(storage, data.file.name));
            }
            let updateData = {
                name: data.name,
                category: data.category,
                price: data.price,
                code: data.code,
                description: data.description,
                show: data.status,
                thumbnail: productImage
            };
            console.log(data);
            await updateDoc(doc(db, "products", id ),updateData);
        }
       
        const req = await getProductsFromFirestore();
        const req1 =  await createProductCaching(req);
    } else {
        // doc.data() will be undefined in this case
        console.log("No such document!");
    }  
    return productImage; 
}
