/*------------------------------------- orders  --------------------------------------------- */

const createDocID = (currentNumber = null)=>{
    const date = new Date();
    if(currentNumber){
        return date.getFullYear() + "" + (date.getMonth()+1) +""+ date.getDate() + "" + currentNumber;
    }else{
        return date.getFullYear() + "" + (date.getMonth()+1) +""+ date.getDate();
    }
    
}

export const updateFeedback = async (orderID,content)=>{
    const _doc = doc(db, "orders", orderID);
    await updateDoc(_doc, {
        feedback: content
    });
}


export const createOrder = async (data)=>{
    let _data = {
        content: JSON.stringify(data.content),
        feedback: "",
        date: Timestamp.fromDate(new Date()),
        status:"Chờ duyệt",
        total:data.total,
        userID: data.userID,
        type: data.section
        
    }
    const _doc = doc(db, "orders", "general");
    const rs = await getDoc(_doc);
    if (rs.exists()) {
        if(typeof(rs.data()[createDocID()]) === "undefined"){
            const docID = createDocID();
            let tmp = {};
            tmp[docID] = 1;
            await setDoc(doc(db, "orders", docID + "0"),_data); 
            await updateDoc(_doc, tmp);
        }else{
            const docID = createDocID(rs.data()[createDocID()]);
            let tmp = {};
            tmp[createDocID()] = rs.data()[createDocID()] + 1;
            await setDoc(doc(db, "orders", docID),_data); 
            await updateDoc(_doc, tmp);
        }
    } else {
        console.log("No such document!");
    }
    
}

export const getOrdersFromFirestore = async (userID)=>{
    const output = {pending:[], confirmed:[], completed: []};
    const q = query(collection(db, "orders"), where("userID", "==", userID));

    const orders = await getDocs(q);
    orders.forEach((doc) => {
    // console.log(doc.id, " => ", doc.data());
        let tmp = doc.data();
        let _date = tmp.date.toDate();
        if(tmp.status === "Chờ duyệt"){
            output["pending"].push({
                date: `${_date.getDate() +"/"+ (_date.getMonth() +1) +"/"+ _date.getFullYear()}`,
                id: doc.id,
                type: tmp.type,
                status: tmp.status,
                content: tmp.content ,
                total: tmp.total  
            });
        }else if(tmp.status === "Đã duyệt"){
            output["confirmed"].push({
                date: `${_date.getDate() +"/"+ (_date.getMonth() +1) +"/"+ _date.getFullYear()}`,
                id: doc.id,
                type: tmp.type,
                status: tmp.status,
                content: tmp.content,
                total: tmp.total
              
            });
        }else{
            output["completed"].push({
                date: `${_date.getDate() +"/"+ (_date.getMonth() +1) +"/"+ _date.getFullYear()}`,
                id: doc.id,
                type: tmp.type,
                status: tmp.status,
                content: tmp.content,
                total: tmp.total  
            });
        }   
    });
    return output;
}

