export const getProviderCaching = async ()=>{
    try{
        const req = await fetch('https://caching-system.herokuapp.com/?appID=dinoshop&action=get-public&data=provider',{
            mode:"cors",
            method: "POST"
        });
        return await req.json();
    } catch (err){
       const req = await getProviderFromFirestore();
       const req1 =  await createProviderCaching(req);
       return req;
    }
}

export const getProviderFromFirestore = async()=>{
    const _data = await getDoc(doc(db, "providers","general"));
    const query = await getDocs(collection(db, "providers"));
    const output = {raw:[], filter: [{text:"Tất cả", value:""}]};
    if (_data.exists()) {
        query.forEach((provider,index)=>{
            if((provider.id !== "general")&&(_data.data()[provider.id] > 0)){
                if(provider.data().active === true){
                    output['raw'].push({name: provider.id, active: provider.data().active});
                    output['filter'].push({text: provider.id, value: provider.id});
                }
            }
        });
    }
    return output;
}

export const addProvider = async(_data)=>{
    const _doc = doc(db, "providers", "general");
    const query = await getDoc(_doc);
    if (query.exists()) {
        const _tmp = {};
        if(typeof(query.data()[_data.name]) === "undefined"){
            _tmp[_data.name] = 1;
        }else{
            _tmp[_data.name] = query.data()[_data.name] + 1;
        }
        await updateDoc(doc(db, "providers", "general" ),_tmp);
    }
    await setDoc(doc(db, "providers", _data.name),{
        active: true,
    });
    const data = await getProviderFromFirestore();
    await createProviderCaching(data);
    return data;
}

