export const createCategory = async (data,mode="w", id=null)=>{
    const _doc = doc(db, "category", "general");
    const query = await getDoc(_doc);
    let providersData = null;
    if (query.exists()) {
        if(mode === "w"){
            await setDoc(doc(db, "category",  "c"+(query.data().total + 1 )), {
                name: data.name,
                provider: data.provider,
                description: data.description,
                createDate: Timestamp.fromDate(new Date()),
                show: data.status
              });
            await updateDoc(_doc, {total: query.data().total + 1 }); 
            providersData = await addProvider({name: data.provider}); 
        }else{
            await updateDoc(doc(db, "category", id ),data);
        }
       
        const req = await getCategoryFromFirestore();
        const req1 =  await createCategoryCaching(req);
    } else {
        // doc.data() will be undefined in this case
        console.log("No such document!");
    }   
    return providersData;
}

export const getCategoryFromFirestore = async ()=>{
    const query = await getDocs(collection(db, "category"));
    let res = [];
    query.forEach((doc) => {
        if(doc.id !== "general"){
            let tmp = doc.data();
            tmp.id = doc.id;
            tmp.createDate = `${tmp.createDate.toDate().getDate() +"/"+ (tmp.createDate.toDate().getMonth() +1) +"/"+ tmp.createDate.toDate().getFullYear()}`
            res.push(tmp);
        }  
    });
    return res;
}


