import { useEffect, useState } from "react"
import TickIcon from '../images/tick.svg'

import './checkbox.css';

const Checkbox = (props)=>{
    const [checked, setChecked] = useState(0);

    useEffect(()=>{
        if(typeof(props.checked) != "undefined"){
            setChecked(props.checked);
        }
    },[props.checked]);

    return(
        <div className={checked === 1 ? "checkbox checked" : "checkbox"} onClick={()=>{
            if(checked === 0){
                setChecked(1);
                if(props.onClick){
                    if(typeof(props.onClick) !== "object"){
                        props.onClick(1);
                    }else{
                        console.log(props.onClick.addSelectedItem(props.itemID));
                    }
                }
            }
            else{
                setChecked(0);
                if(props.onClick){
                    if(typeof(props.onClick) !== "object"){
                        props.onClick(0);
                    }else{
                        console.log(props.onClick.removeSelectedItem(props.itemID));
                    }
                }
            }
        }}>
            <img src={TickIcon} alt="dấu tick"/>
        </div>
    );
}

export default Checkbox;
