import './dropdown.css'
import { useEffect, useRef, useState  } from "react"
import ArrowDown from '../images/arrown-down.svg'

const Dropdown = (props)=>{
    const [value,setValue] = useState(typeof(props.placeholder) !== "undefined" ? props.placeholder : "");
    const [showOptions, setShowOptions] = useState(0);
    const dropdownElem = useRef();
    let options = [];

    const checkClickedOutside = (event)=>{
        if(dropdownElem.current !== null){
            if (!dropdownElem.current.contains(event.target)) {
                if(showOptions === 1){setShowOptions(0);}
            }
        }else{
            console.log("null");
        }
      
    }

    useEffect(()=>{
        
        document.addEventListener('click', checkClickedOutside);
        
        return ()=>{
            document.removeEventListener('click',checkClickedOutside);
        }
    });

    if(typeof(props.options) !== "undefined"){
        if(props.options.length > 0){
            props.options.forEach(option => {
                options.push(<div className="option" onClick={()=>{
                    if(typeof(props.onClick) !== "undefined"){
                        props.onClick(option.value);
                    }
                    setValue(option.text);
                    setShowOptions(0);
                }}>{option.text}</div>)
            });
        }
    }

    return(
        <div className="dropdown" ref={dropdownElem}>
            <div className="displayer" onClick={()=>{setShowOptions(1);}}>
                <div className="label">{value}</div>
                <div className="icon">
                    <img src={ArrowDown} />
                </div>
            </div>
            <div className={showOptions === 1 ? "option-list show" : "option-list"}>
                <div className="scroller">
                    {options}
                </div>
            </div>
        </div>
    )
}

export default Dropdown;