import { useEffect,useState } from "react"
import './quantity-button.css';
import {GetCartID}  from './ultility';
import CartContext from "../Contexts/Cart";
import PlusIcon from '../images/plus.svg'
import RemoveIcon from '../images/remove.svg'
import RemoveUnactiveIcon from '../images/remove-unactive.svg'
import RemoveAllIcon from '../images/remove-all.svg'
import AddToCartIcon from '../images/add-to-cart.svg'


const QuantityButton = (props) =>{
    const [quantity,setQuantity] = useState(props.quantity ? props.quantity : 0  );
    const [isAddedToCart, setIsAddedToCart] = useState(props.addedToCart? props.addedToCart : 0 );

    const increaseQuantity = ()=>{
        setQuantity(quantity + 1);
    }

    const decreaseQuantity = ()=>{
        if(quantity > 0){
            setQuantity(quantity - 1);
        }
    }

    useEffect(()=>{
        if(props.updateHandler){
            props.updateHandler(quantity, isAddedToCart);
        }
    });

    return(
        <CartContext.Consumer>
            {
                context =>(
                    <div className="button-group">
                        <div className="quantity-button-group">
                            <div className={props.mode !== "order-review"? "button" : "button hide"} onClick={()=>{
                                if(props.mode !== "order-review"){
                                    decreaseQuantity();
                                    const currentQuantity = context.getCartQuantityHandler();
                                    if ((quantity === 1)&&(currentQuantity > 0)){
                                        context.addToCartHandler(currentQuantity - 1);
                                        setIsAddedToCart(0);
                                    }
                                    //Update số lượng khi đang ở giao diện giỏ hàng
                                    if(typeof(props.cartData) !== "undefined"){
                                        const index = GetCartID(props.itemID);
                                        let _tmp = props.cartData;
                                        _tmp.storedData[index[0]].items[index[1]].quantity = (quantity -1);
                                        props.cartData.updateDataHandler(JSON.stringify(_tmp.storedData));
                                    }
                                    //---
                                    if(isAddedToCart === 1){
                                        if(typeof(props.productInfo) !== "undefined"){
                                            if(props.isCartView === true){
                                                const section = typeof(props.sectionID) !== "undefined" ? props.sectionID : GetCartID(props.itemID)[0]; 
                                                context.updateQuantityListHandler(props.productInfo.id, quantity - 1, props.productInfo.index, section);
                                            }else{
                                                //Update số lượng vào cartData - cây thông tin chứa danh sách cart ở mọi vie
                                                if(props.productInfo.index !==  null){
                                                    const section = typeof(props.sectionID) !== "undefined" ? props.sectionID : GetCartID(props.itemID)[0]; 
                                                    context.updateCartHandler(props.productInfo.index, quantity - 1);
                                                    context.updateQuantityListHandler(props.productInfo.id, quantity - 1, props.productInfo.index, section);
                                                }
                                                //---
                                            }
                                        } 
                                    }
                                } 
                            }}>
                                <img src={quantity > 0 ? RemoveIcon : RemoveUnactiveIcon} style={{opacity: quantity > 0 ? 1 : 0.3 }} alt="-"/>
                            </div>
                            <div className="quantity-displayer">
                                <div>{props.mode === "order-review" ? "x" + quantity : quantity}</div>
                            </div>
                            <div className={props.mode !== "order-review"? "button" : "button hide"} onClick={()=>{
                                if(props.mode !== "order-review"){
                                    increaseQuantity();
                                    //Update số lượng khi đang ở giao diện giỏ hàng
                                    if(typeof(props.cartData) !== "undefined"){
                                        const index = GetCartID(props.itemID);
                                        let _tmp = props.cartData;
                                        _tmp.storedData[index[0]].items[index[1]].quantity = (quantity + 1);
                                        props.cartData.updateDataHandler(JSON.stringify(_tmp.storedData));
                                    }
                                    //---
                                    if(isAddedToCart === 1){
                                        if(typeof(props.productInfo) !== "undefined"){
                                            if(props.isCartView === true){
                                                const section = typeof(props.sectionID) !== "undefined" ? props.sectionID : GetCartID(props.itemID)[0]; 
                                                context.updateQuantityListHandler(props.productInfo.id, quantity + 1,props.productInfo.index, section);
                                            }else{
                                                //Update số lượng vào cartData - cây thông tin chứa danh sách cart ở mọi view
                                                if(props.productInfo.index !== null){
                                                    const section = typeof(props.sectionID) !== "undefined" ? props.sectionID : GetCartID(props.itemID)[0]; 
                                                    context.updateCartHandler(props.productInfo.index, quantity + 1);
                                                    context.updateQuantityListHandler(props.productInfo.id, quantity + 1,props.productInfo.index, section);
                                                }
                                                //---
                                            }
                                        } 
                                    }
                                }  
                            }}>
                                <img src={PlusIcon}  alt="+"/>
                            </div>
                        </div>
                        <div className="add-to-cart-holder">
                            <div className={props.mode !== "order-review"? "add-to-cart" : "add-to-cart hide"} style={{backgroundColor: isAddedToCart === 1 ? "orangered" : (quantity > 0 ? "orangered" : "silver")}}
                                onClick = {(()=>{
                                    if(props.mode !== "order-review"){
                                        if (quantity > 0){
                                            if(isAddedToCart === 0){
                                                const currentQuantity = context.getCartQuantityHandler();
                                                let productInfo = props.productInfo;
                                                productInfo.quantity = quantity;
                                                const index = context.addToCartHandler(1 + currentQuantity, props.sectionID, productInfo); // add vào giỏ
                                                context.updateQuantityListHandler(props.productInfo.id, quantity, index, props.sectionID);
                                                setIsAddedToCart(1);
                                            }
                                            else{
                                                const currentQuantity = context.getCartQuantityHandler();
                                                context.addToCartHandler(currentQuantity - 1);
                                                setIsAddedToCart(0);
                                                setQuantity(0);
                                                if(typeof(props.cartData) !== "undefined"){
                                                    const index = GetCartID(props.itemID);
                                                    let _tmp = props.cartData;
                                                    _tmp.storedData[index[0]].items[index[1]] = "";
                                                    props.cartData.updateDataHandler(_tmp.storedData);
                                                }
                                                
                                            }
                                        }
                                    }  
                                })}
                            >
                                <img src={isAddedToCart === 1 ? RemoveAllIcon :  AddToCartIcon}  alt="add"/>
                            </div>
                        </div>
                    </div>
                )
            }
        </CartContext.Consumer>
    );
}

export default QuantityButton;