import { Link } from "react-router-dom";
import './footer.css';

const footer = (props)=>{

    const about = [];
    const product = [];


    props.aboutData.forEach((item,index) => {
        about.push(
            <Link key={index} to={item.url} className="link">{item.text}</Link>
        );  
    });

    props.productData.forEach((item,index) => {
        product.push(
            <Link key={index} to={item.url} className="link">{item.text}</Link>
        );  
    });


    return(
        <div className="footer">
            <div className="container">
                <div className="column">
                    <img src={props.info.logo} alt="logo"/>
                    <p>{props.info.name}</p>
                    <p><span>Hotline:</span>{props.info.hotline}</p>
                    <p><span>Email:</span>{props.info.email}</p>
                    <p><span>Địa chỉ</span>{props.info.address}</p>
                </div>
                <div className="column">
                    <p><span>Về chúng tôi</span></p>
                    {about}
                </div>
                <div className="column">
                    <p><span>Danh mục sản phẩm</span></p>
                    {product}
                </div>
            </div>
        </div>
    );
}

export default footer;
