import { useEffect, useRef, useState  } from "react";
import './radio-button.css'

const RadioButton = (props)=>{
    const [isSelected,setIsSelected] = useState(typeof(props.selected) !== "undefined" ? props.selected : 0);
    
    useEffect(()=>{
        if(typeof(props.selected) !== "undefined"){
            setIsSelected(props.selected);
        }
    },[props.selected]);

    return (
        <div className="radio-button" 
            onClick={
                ()=>{
                    if(typeof(props.onClick) !== "undefined"){
                        props.onClick(props.index, props.haveSubSelection);
                    }
                }
        }>
            <div className="radio">
                <div className={isSelected === 1 ? "circle selected" : "circle"}></div> 
            </div>
            <div className="label">
                <p>{props.label}</p>
                <p>{props.subLabel}</p>
            </div>
        </div>
    );
}

export default RadioButton;