const Home = ()=>{
  const [showPopup, setShowPopup] = useState(0);
  const [popupName, setPopupName] = useState("feedback");
  const [data,setData] = useState("");
  const [category,setCategory] = useState("");
  let renderCount = useRef(0);
  const showPrintOrderSuccessPopup = ()=>{
      setPopupName('print-order-success');
      setShowPopup(1);
  }

  const hidePopup = ()=>{
      setShowPopup(0);
  }



  useEffect(()=>{
    if(renderCount.current === 0){
      const _getProducts = async ()=>{
        let products = await getAllProducts();
        let category = await getCategoryCaching();
        setData(products);
        setCategory(category);
        console.log("first-render");
        console.log(products);
        console.log(category);
      }
      _getProducts();
    }

    renderCount.current++;
  })

  const renderProductList = ()=>{
    let output = []
    if((data !== "")&&(category !== "")){
      category.forEach(cat => {
        let tmp = [];
        data.forEach(prod =>{
          if(prod.category === cat.name){
            tmp.push(prod);
          }
        });
        output.push(<ProductList productList = {tmp} route= "/van-phong-pham" title={cat.name} limit={20} sectionID={0}/>)
      });
    }
    return output;
  }

  return(
      <div>
          <Carousel imageList={carouselData}></Carousel>
          {/* <ProductList productList = {data} route= "/van-phong-pham" title="Văn phòng phẩm" limit={20} sectionID={0}/>
          <ProductList productList = {fakeProductData} route= "/nhu-yeu-pham"  title="Nhu yếu phẩm" limit={4} sectionID={1}/> */}
          {renderProductList()}
          <PhotocopyRequest 
            title="Photocopy/in tài liệu" 
            thumbnail= {"https://wallpapercave.com/wp/wp7411799.jpg"}
            note="Tài liệu sẽ được nhận lại vào chiều thứ thứ 3, thứ 6 hàng tuần vào lức 17:00 tại văn phòng."
            choices = {choicesData}
            handlers={{showPrintOrderSuccess: showPrintOrderSuccessPopup}}
          ></PhotocopyRequest>
          <Popup 
              name={popupName} 
              data={""} 
              show={showPopup} 
              handlers={{
                  hide: hidePopup 
          }}></Popup>
      </div>
  );
};

export default Home;