import './order-list.css'
import Button from './button'
import { useEffect, useState } from 'react';
import { Link } from "react-router-dom";
import FormatNumber from "./ultility";


const Order = (props)=>{

    if(props.status === "Chờ duyệt"){
        return(
            <div className="order">
                <div>{props.date}</div>
                <div>{props.id}</div>
                <div>{props.type}</div>
                <div>{FormatNumber(props.total)}</div>
                {/* <div>{props.status}</div> */}
                <div>
                    <Link to="/gio-hang" class="link">
                        <Button isWeak = {true} label="Xem chi tiết" onClick={
                            ()=>{
                                if(typeof(props.handlers)!== "undefined"){
                                    props.handlers.switchCartMode(1);
                                    props.handlers.addDataForReviewCartHandler(props.type, props.data);
                                    // props.handlers.debugOrderReviewData();
                                    // props.handlers.gotoOrderDetail();
                                }
                            }
                        }/>
                    </Link>    
                </div>
                <div><Button label="Hủy đơn" onClick={()=>{
                    if(typeof(props.handlers)!== "undefined"){
                        props.handlers.showCancel({id: props.id});
                    }
                }
                }/></div>
            </div>
        );
    }else{
        return(
            <div className="order">
                <div>{props.date}</div>
                <div>{props.id}</div>
                <div>{props.type}</div>
                <div>{FormatNumber(props.total)}</div>
                {/* <div>{props.status}</div> */}
                <div><Button isWeak = {true} label="Xem chi tiết" onClick={
                    ()=>{
                        if(typeof(props.handlers)!== "undefined"){
                            props.handlers.switchCartMode(1);
                            props.handlers.addDataForReviewCartHandler(props.type, props.data);
                            // props.handlers.debugOrderReviewData();
                            // props.handlers.gotoOrderDetail();
                        }
                    }
                }/></div>
                <div><Button label="Báo cáo" onClick={()=>{
                    if(typeof(props.handlers)!== "undefined"){
                        props.handlers.showFeedback({ orderID: props.id, orderType: props.type, orderDate: props.date});
                    }
                }
                }/></div>
            </div>
        );
    }
}


const Tab = (props)=>{
    let orders = []; 
    const [active,setActive] = useState(props.active);
    useEffect(()=>{
        setActive(props.active);
    },[props.active]);

    if(typeof(props.data) !== "undefined"){
        props.data.forEach(order => {
            orders.push(<Order date={order.date} id={order.id} type={order.type} total={order.total} status={order.status} data={order.content} handlers={typeof(props.handlers)!== "undefined" ? props.handlers : null}></Order>);
        });
    }

    return(
        <div className={active === 1 ? "order-list show" : "order-list"}>
            <h3>{props.title}</h3>
            <div className="order header">
                <div>Ngày đặt</div>
                <div>Mã đơn hàng</div>
                <div>Loại đơn</div>
                <div>Tổng tiền</div>
                {/* <div>Trạng thái</div> */}
                <div></div>
                <div></div>
            </div>
            <hr/>
            {orders}
        </div>
    );
}

const Tabs = (props)=>{

    const [activeTab , setActiveTab] = useState(1);

    return(
        <div className="order-tabs">
            <div className="tab-header">
                <div className={activeTab === 1 ? "active" : ""} onClick={()=>{setActiveTab(1);}}>Chờ duyệt</div>
                <div className={activeTab === 2 ? "active" : ""} onClick={()=>{setActiveTab(2);}}>Đã duyệt</div>
                <div className={activeTab === 3 ? "active" : ""} onClick={()=>{setActiveTab(3);}}>Hoàn thành</div>
            </div>
            <div className="tab-holder">
                <Tab data={props.data.pending} active={activeTab === 1 ? 1 : 0} handlers={typeof(props.handlers)!== "undefined" ? props.handlers : null}></Tab>
                <Tab data={props.data.confirmed} active={activeTab === 2 ? 1 : 0} handlers={typeof(props.handlers)!== "undefined" ? props.handlers : null}></Tab>
                <Tab data={props.data.completed} active={activeTab === 3 ? 1 : 0} handlers={typeof(props.handlers)!== "undefined" ? props.handlers : null}></Tab>
            </div>
        </div>
    );
}


const OrdersTab = (props) => {
    return(
        <div className="order-tab">
            <div className="container">
                <h3>Đơn hàng của bạn</h3>
                <Tabs data={props.data} handlers={typeof(props.handlers)!== "undefined" ? props.handlers : null}></Tabs>
            </div>
        </div>
    );
   
}

export default OrdersTab;