const History = ()=>{

    const [showPopup, setShowPopup] = useState(0);
    const [orderData,setOrderData] = useState({});
    const [popupName, setPopupName] = useState("feedback");
    const [data,setData] = useState("");
    let renderCount = useRef(0);

    const showFeedbackPopup = (_data)=>{
        setPopupName("feedback");
        setOrderData(_data);
        setShowPopup(1);
    }

    const showCancelOrderPopup = (_data)=>{
        setPopupName('cancel-order');
        setOrderData(_data);
        setShowPopup(1);
    }

    const showFeedbackSuccessPopup = (orderID,content)=>{
        updateFeedback(orderID,content)
        setPopupName("feedback-success");
    }

    const hidePopup = ()=>{
        setShowPopup(0);
    }

    const gotoOrderDetailScreen = ()=>{
        window.location.href = "/gio-hang";
    }


    useEffect(()=>{
        if(renderCount.current === 0){
          const _getOrders = async ()=>{
            let rs = await getOrders("FSC123456");
            setData(rs);
            console.log(rs);
            console.log("first-render");
          }
          _getOrders();
        }else{
            console.log("render");
        }   
       
        renderCount.current++;
      })



    return(
        <CartContext.Consumer>
            {
                context=>(
                    <div  style={{marginTop: "30px"}}>
                            <StudentInfo 
                                name="Trần Minh Hùng" 
                                id="FSC10252" 
                                class="11A3" 
                                years="2021-2022" 
                                phone="0907689756" 
                                email="tranminhhung10252@fpt.edu.vn"
                                avatar="https://getbootstrap.com.vn/blog/wp-content/uploads/2020/11/kieu-toc-hoc-sinh-two-block.jpg"
                            ></StudentInfo>
                            <OrderList data={data} handlers={{
                                showFeedback: showFeedbackPopup, 
                                showCancel: showCancelOrderPopup, 
                                gotoOrderDetail: gotoOrderDetailScreen,
                                addDataForReviewCartHandler: context.addDataForReviewCartHandler,
                                switchCartMode:  context.switchCartModeHandler,
                                debugOrderReviewData: context.debugOrderReviewData
                            }}></OrderList>
                            <Popup 
                                name={popupName} 
                                data={orderData} 
                                show={showPopup} 
                                handlers={{
                                    showFeedbackSuccess: showFeedbackSuccessPopup, 
                                    hide: hidePopup
                            }}></Popup>
                    </div>
                )
            }
        </CartContext.Consumer>
      
    );
};

export default History;