import { useEffect, useRef, useState  } from "react";
import './admin.css';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import Header from "../components/header";
import AdminCategory from "./admin-category";
import AdminProduct from "./admin-product";

const Admin = ()=>{
    const menuItem = [
        <Link to="/" className="link" key="1">Quản lý đơn hàng</Link>,
        <Link to="/admin/quan-ly-danh-muc" className="link" key="2" >Quản lý danh mục</Link>,
        <Link to="/admin/quan-ly-san-pham" className="link" key="3">Quản lý sản phẩm</Link>,
        <Link to="/" className="link" key="4">Quản lý in ấn</Link>,
        <Link to="/in-tai-lieu" className="link" key="5">Quản lý báo cáo</Link>,
    ];
    // const menuItem = [
    //     <div className="link" onClick={()=>{ history.push('/admin/quan-ly-danh-muc');}}>Quản lý danh mục</div>,
    //     <div className="link" onClick={()=>{history.push('/admin/quan-ly-san-pham');}}>Quản lý sản phẩm</div>,
    // ]

    return(
        <Router>
            <div className="admin">
                <div className="container">
                    <Header menuItems={menuItem}></Header>
                    <div className="view">
                        <Switch>
                            <Route exact path="/admin/quan-ly-danh-muc">   
                                <AdminCategory/>
                            </Route>
                            <Route exact path="/admin/quan-ly-san-pham">   
                                <AdminProduct/>
                            </Route>
                        </Switch>
                    </div>
                </div>
            </div>
        </Router>
    );
}

export default Admin;