
import './App.css';
import Admin from "./views/admin"
import Student from './views/student';

import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

function App() {
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route exact path="/">
            <Student />
          </Route>
          <Route exact path="/admin/">
            <Admin/>
          </Route>
        </Switch>
      </div>
    </Router> 
  );
}

export default App;
