import React, { useEffect, useState } from "react"
import Button from "./button";
import './photocopy-request-block.css';

const LinkInput = () =>{
    const [value, setValue] = useState(1);
    return(
        <div className="link-input">
            <div className="container">
                <div className="quantity">
                    <p>Số lượng</p>
                    <input type="text" value={value} onChange={(event)=>{setValue(event.target.value);}}></input>
                </div>
                <div className="document-link">
                    <p>Link tài liệu</p>
                    <input type="text" placeholder="Nhập link tài liệu"></input>
                </div>
            </div>    
           <p>Bạn vui lòng upload tài liệu vào link: https://www.driver.com.vn/d/in-dinoshop. Vui lòng đặt tên theo cấu trúc: [Ngày in] - Họ tên - Lớp - mssv.</p>
        </div>
    );
}

const Option = (props)=>{
    const [isSelected,setIsSelected] = useState(props.selected);
    useEffect(()=>{
        setIsSelected(props.selected);
    },[props.selected]);
    return (
        <div className="option" 
            onClick={
                ()=>{
                    props.onClickHandler(props.index, props.haveSubSelection);
                }
        }>
            <div className="radio">
                <div className={isSelected === 1 ? "circle selected" : "circle"}></div> 
            </div>
            <div className="label">
                <p>{props.label}</p>
                <p>{props.subLabel}</p>
            </div>
        </div>
    );
}


const Choices = (props)=>{
    const choices = [];
    const [selectedIndex, setSelectedIndex] = useState(0);
    const [showSubSelection, setShowSubSelection] = useState(false);
    const updateChoice = (index, haveSubSelection) =>{
        setSelectedIndex(index);
        setShowSubSelection(haveSubSelection);
    };

    if(props.choices){
        props.choices.forEach((choice,index) => {
            choices.push(
                <Option 
                    key= {index}
                    index = {index} 
                    label={choice.label} 
                    subLabel = {choice.subLabel} 
                    selected={selectedIndex === index ? 1 : 0} 
                    onClickHandler ={updateChoice}
                    haveSubSelection = {choice.haveSubSelection}
                ></Option>
            );
        });
    }

    return(
        <div className="choice">
            <p>{props.label}</p>
            <div className="choices">
                {choices}
            </div>
            {showSubSelection === true ? <LinkInput/> : null}
        </div>
    );
}

const PhotocopyRequest = (props)=>{

    const choices =  [];

    if(props.choices){
        props.choices.forEach((choice,index)=>{
            choices.push(<Choices key={index} label={choice.label} choices = {choice.choices}></Choices>)
        });
    }

    return(
        <div className="photocopy-section">
            <h2>{props.title}</h2>
            <div className="container">
                <div className="thumbnail">
                    <img src={props.thumbnail} alt="ảnh tiêu đề"/>
                </div>
                <div className="content">
                    <h3>Đăng kí</h3>
                    <p>{props.note}</p>
                    {choices}
                    <hr/>
                    <Button label="Đăng ký" onClick={typeof(props.handlers)!=="undefined" ? props.handlers.showPrintOrderSuccess : null}/>
                    {/* <div className="register-button">Đăng kí</div> */}
                </div>
            </div>
            
        </div>
    );
}

export default PhotocopyRequest;