import './student-info.css';

const StudentInfo = (props)=>{
    return(
        <div className = "student-info">
            <div className="container">
                <h3>Thông tin học sinh</h3>
                <div className="info-group">
                    <div className="info" style={{flex: 1}}><div className="avatar"><img src={props.avatar} alt="hình học sinh"/></div></div>
                    <div className="info"><div>Họ và tên:</div><div>{props.name}</div></div>
                    <div className="info"><div>MSHS:</div><div>{props.id}</div></div>
                    <div className="info"><div>Lớp:</div><div>{props.class}</div></div>
                    <div className="info"><div>Niên khóa:</div><div>{props.years}</div></div>
                    <div className="info"><div>Số điện thoại:</div><div>{props.phone}</div></div>
                    <div className="info" style={{flex:3}}><div>Email:</div><div>{props.email}</div></div>
                </div>
            </div>
        </div>
    );
}

export default StudentInfo;