import { useEffect, useState } from "react"
import { Link } from "react-router-dom";
import './carousel.css';

const Slide = (props)=>{
    const [isActive, setIsActive] = useState(props.active);

    useEffect(()=>{
        setIsActive(props.active);
    },[props.active]);

    return(
        <div className={isActive === 1 ? "slide active" : "slide"}>
            <div className="container">
                <Link to={props.link}><img className={props.direction === "horizontal" ? "horizontal" : "vertical"} src={props.image} alt="hình banner"/></Link>
            </div>        
        </div>
    );
}


const Carousel = (props)=>{
    const slides = [];
    const indicators = [];
    const [activeSlide, setActiveSlide] = useState(0);

    let imageChecker = new Image();


    if(props.imageList){
        props.imageList.forEach((image,index) => {
            imageChecker.src = image.image;
            const ratio = (imageChecker.width/imageChecker.height);
            slides.push(<Slide key={index} link={image.link} image={image.image} active={activeSlide === index ? 1 : 0} direction={ratio < 1.62 ? "horizontal" : "vertical"}></Slide>);
            indicators.push(<div key={"i"+index} className={activeSlide === index ? "indicator active" : "indicator"}></div>)
        });
    }

    useEffect(()=>{
        const switchSlide = setInterval(()=>{
            if(activeSlide !== 2){
                setActiveSlide(activeSlide +1);
            }else{
                setActiveSlide(0);
            }
            clearInterval(switchSlide);    
        },3000);

    })

    return(
        <div className="carousel">
            <div className="container">
                {slides}
            </div>
            <div className="indicators">
                {indicators}
            </div>
        </div>
    );
}

export default Carousel;