import { useEffect, useRef, useState  } from "react";
import {createProduct, getProductCaching, getCategoryCaching} from '../models/data'
import Button from '../components/button';
import Popup from '../components/popup';
import './admin.css'
import Dropdown from "../components/dropdown";
import Paginator from "../components/paginator";

const TableRow= (props)=>{
    let elem = [];
    if(typeof(props.data) !== "undefined"){
        props.data.forEach((item,index) => {
            elem.push(<div className="table-col" style={{flex: props.size[index], fontWeight: typeof(props.header) !== "undefined"? 600 : 500}}>{item}</div>);
        });
    }

    return(
        <div className="table-row" style={props.style}>
            {elem}
        </div>
    )
}


const AdminProduct = () => {
    const [showPopup, setShowPopup] = useState(0);
    const [popupName, setPopupName] = useState("create-product");
    const [productData, setProductData] = useState([]);
    const [categoryData,setCategoryData] = useState([]);
    const [productInfo, setProductInfo] = useState(null);
    const [renderCount,setRenderCount] = useState(0);
    const [selectedCategory, setSelectedCategory] = useState("");
    const [selectedStatus, setSelectedStatus] = useState("")
    const update = useRef(0);
    const currentProductID = useRef(null);

    let categoryRow = [];

    const showCreateCategoryPopup = ()=>{
        setShowPopup(1);
    }

    const hideCreateCategoryPopupHandler = ()=>{
        setShowPopup(0);
        setProductInfo(null);
    }

    const switchToUpdateModeHandler = ()=>{
        update.current = 1;
    }

    const createProductHandler = async (data,callback = null)=>{
        let tmp = productData;
        let date = new Date;
        const thumbnail = await createProduct(data,update.current === 1? "u" : "w",currentProductID.current);
        if(update.current !== 1){
            tmp.push({
                id: currentProductID.current,
                name: data.name,
                description: data.description,
                createDate: date.getDate() +"/"+ (date.getMonth() +1) +"/"+ date.getFullYear(),
                show: data.status,
                code: data.code,
                category: data.category,
                thumbnail: thumbnail,
                price: data.price
            });
            setProductData(tmp);
        }else{
            for(let i = 0; i < tmp.length; i++){
                if(tmp[i].name === data.name){
                    tmp[i] = {
                        id: currentProductID.current,
                        name: data.name,
                        description: data.description,
                        createDate: date.getDate() +"/"+ (date.getMonth() +1) +"/"+ date.getFullYear(),
                        show: data.status,
                        code: data.code,
                        category: data.category,
                        thumbnail: thumbnail,
                        price: data.price
                    }
                }
            }
            setProductData(tmp);
        }
        currentProductID.current = null;
        update.current = 0;
        setShowPopup(0);
        if(callback !== null){
            callback();
        }
        // setProviderData(providers['filter']);
    }

    const bindProductDataHandler = (data)=>{
        setProductInfo(JSON.stringify(data));
        setShowPopup(1);
    }

    useEffect(async ()=>{
        if(renderCount === 0){
            let tmpCategoryData = [];
            const data = await getProductCaching();
            const _category = await getCategoryCaching();
            console.log(_category);
            _category.forEach((cat,index)=>{
                tmpCategoryData.push({text: cat.name, value: cat.name});
            });
            setCategoryData(tmpCategoryData);
            setProductData(data);
            setRenderCount(1);
        }
    },[renderCount]);

    if(productData.length > 0){
        productData.forEach((product,index)=>{
            if(selectedCategory !== ""){
                if((product.category === selectedCategory)&&(selectedStatus === true)&&(product.show === true)){
                    categoryRow.push(<TableRow data={[product.code,<img src={product.thumbnail}/>,product.name,product.price,product.description, product.category,product.show === true ? "Hiển thị" : "Đang ẩn", product.createDate,[
                        <Button label="sửa" onClick={()=>{
                            bindProductDataHandler({
                                id: product.id,
                                name: product.name,
                                description: product.description,
                                category: product.category,
                                show: product.show,
                                price: product.price,
                                code: product.code,
                                thumbnail:  product.thumbnail
                            });
                            console.log(product.id);
                            update.current = 1;
                            currentProductID.current = product.id;
                        }}/>, 
                        <Button label="xóa" isWeak={true}/>
                    ]]} size={[2,2,5,2,5,3,2,2,2]}/>);
                }
                else if ((product.category === selectedCategory)&&(selectedStatus === false)&&(product.show === false)){
                    categoryRow.push(<TableRow data={[product.code,<img src={product.thumbnail}/>,product.name,product.price,product.description, product.category,product.show === true ? "Hiển thị" : "Đang ẩn", product.createDate,[
                        <Button label="sửa" onClick={()=>{
                            bindProductDataHandler({
                                id: product.id,
                                name: product.name,
                                description: product.description,
                                category: product.category,
                                show: product.show,
                                price: product.price,
                                code: product.code,
                                thumbnail:  product.thumbnail
                            });
                            console.log(product.id);
                            update.current = 1;
                            currentProductID.current = product.id;
                        }}/>, 
                        <Button label="xóa" isWeak={true}/>
                    ]]} size={[2,2,5,2,5,3,2,2,2]}/>);
                }else if ((product.category === selectedCategory)&&(selectedStatus === "")){
                    categoryRow.push(<TableRow data={[product.code,<img src={product.thumbnail}/>,product.name,product.price,product.description, product.category,product.show === true ? "Hiển thị" : "Đang ẩn", product.createDate,[
                        <Button label="sửa" onClick={()=>{
                            bindProductDataHandler({
                                id: product.id,
                                name: product.name,
                                description: product.description,
                                category: product.category,
                                show: product.show,
                                price: product.price,
                                code: product.code,
                                thumbnail:  product.thumbnail
                            });
                            console.log(product.id);
                            update.current = 1;
                            currentProductID.current = product.id;
                        }}/>, 
                        <Button label="xóa" isWeak={true}/>
                    ]]} size={[2,2,5,2,5,3,2,2,2]}/>);
                }
            }else{
                if(selectedStatus !== ""){
                    if((selectedStatus === true)&&(product.show === true)){
                        categoryRow.push(<TableRow data={[product.code,<img src={product.thumbnail}/>,product.name,product.price,product.description, product.category,product.show === true ? "Hiển thị" : "Đang ẩn", product.createDate,[
                            <Button label="sửa" onClick={()=>{
                                bindProductDataHandler({
                                    id: product.id,
                                    name: product.name,
                                    description: product.description,
                                    category: product.category,
                                    show: product.show,
                                    price: product.price,
                                    code: product.code,
                                    thumbnail:  product.thumbnail
                                });
                                console.log(product.id);
                                update.current = 1;
                                currentProductID.current = product.id;
                            }}/>, 
                            <Button label="xóa" isWeak={true}/>
                        ]]} size={[2,2,5,2,5,3,2,2,2]}/>);
                    }else if((selectedStatus === false)&&(product.show !== true)){
                        categoryRow.push(<TableRow data={[product.code,<img src={product.thumbnail}/>,product.name,product.price,product.description, product.category,product.show === true ? "Hiển thị" : "Đang ẩn", product.createDate,[
                            <Button label="sửa" onClick={()=>{
                                bindProductDataHandler({
                                    id: product.id,
                                    name: product.name,
                                    description: product.description,
                                    category: product.category,
                                    show: product.show,
                                    price: product.price,
                                    code: product.code,
                                    thumbnail:  product.thumbnail
                                });
                                console.log(product.id);
                                update.current = 1;
                                currentProductID.current = product.id;
                            }}/>, 
                            <Button label="xóa" isWeak={true}/>
                        ]]} size={[2,2,5,2,5,3,2,2,2]}/>);
                    }
                }else{
                    categoryRow.push(<TableRow data={[product.code,<img src={product.thumbnail}/>,product.name,product.price,product.description, product.category,product.show === true ? "Hiển thị" : "Đang ẩn", product.createDate,[
                        <Button label="sửa" onClick={()=>{
                            bindProductDataHandler({
                                id: product.id,
                                name: product.name,
                                description: product.description,
                                category: product.category,
                                show: product.show,
                                price: product.price,
                                code: product.code,
                                thumbnail:  product.thumbnail
                            });
                            console.log(product.id);
                            update.current = 1;
                            currentProductID.current = product.id;
                        }}/>, 
                        <Button label="xóa" isWeak={true}/>
                    ]]} size={[2,2,5,2,5,3,2,2,2]}/>);
                }
            }
        });
    }

    return (
        <div className="admin-view">
            <div className="header">
                <div className="title">
                    <h3>Quản lý sản phẩm / sản phẩm</h3>
                </div>
                <div className="button-group">
                    <Button label="Thêm sản phẩm" onClick = {showCreateCategoryPopup}></Button>
                </div>
            </div>
            <div className="table">
                <div className="table-header">
                   <div className="search"></div>
                   <div className="filter-group">
                       <Dropdown placeholder="Danh mục" options={[{text:"Tất cả danh mục", value: ""}].concat(categoryData)} onClick = {(value)=>{setSelectedCategory(value);}}/>
                       <Dropdown placeholder="Trạng thái" options={[{text:"Tất cả trạng thái", value:""},{text:"Hiển thị", value:true},{text:"Ẩn", value:false}]} onClick = {(value)=>{setSelectedStatus(value);}}/>
                   </div>
                </div>
                <div className="table-data">
                    <TableRow data={["Mã sản phẩm","Hình","Tên sản phẩm","Giá","Mô tả","Danh mục","Trạng thái","Ngày CN","" ]} size={[2,2,5,2,5,3,2,2,2]} header={true} style={{maxWidth: '90.5%'}}/>
                    <hr/>
                    <div className="table-content">
                        {categoryRow}
                    </div>
                    <Paginator total={3}/>
                </div>
            </div>
            <Popup
                name={popupName} 
                show={showPopup}
                data={productInfo}
                dropdownData={{
                    category: categoryData
                }}
                handlers= {{
                    hideCreateCategoryPopup: hideCreateCategoryPopupHandler, 
                    createProduct: createProductHandler,
                    switchToUpdateMode: switchToUpdateModeHandler,
                    updateFlag : update.current,
                }}
            ></Popup>
        </div>
    );
}

export default AdminProduct;