
import { useEffect, useRef, useState  } from "react";
import Button from './button';
import Dropdown from "./dropdown";
import './popup.css'
import RadioButton from './radio-button';


const FeedbackContent = (props)=>{

    const [content,setContent] = useState("");

    const updateFeedback = ()=>{
        if(typeof(props.handlers)!=="undefined"){
            props.handlers.showFeedbackSuccess(props.orderID,content);
        }
    }

    return(
        <div className="feedback">
            <h3>Báo cáo đơn hàng</h3>
            <p>{props.orderID}-{props.orderType}</p>
            <p><span>Ngày đặt hàng:</span> {props.orderDate}</p>
            <textarea value={content} onChange={(event)=>{setContent(event.target.value)}}></textarea>
            <div className="buttons">
                <Button label="Hủy" isWeak={true} onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hide : null}/>
                <Button label="Gửi báo cáo" onClick={typeof(props.handlers)!=="undefined" ? updateFeedback : null}/>
            </div>
            
        </div>
    );
}

const FeedbackSuccessContent = (props)=>{
    return(
        <div className="feedback">
            <p style={{textAlign:"center"}}>Bạn đã gửi feedback thành công</p>
            <div className="buttons">
                <Button label="Đóng" onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hide : null}/>
            </div>
            
        </div>
    );
}

const CancelContent = (props)=>{
    return(
        <div className="feedback">
            <p style={{textAlign:"center"}}>Bạn có chắc chắn muốn hủy đơn hàng này ({props.id}) hay không?</p>
            <div className="buttons">
                <Button label="Không" isWeak={true} onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hide : null}/>
                <Button label="Có" onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hide : null}/>
            </div>
        </div>
    );
}


const ConfirmationContent = (props)=>{
    return(
        <div className="feedback">
            <div className="row">
                <div className="image">
                    <img src={typeof(props.data) !== "undefined" ? props.data.confirmationImage: "" } alt="hình trang trí"></img>
                </div>
                <div className="content">
                    {typeof(props.data) !== "undefined" ? props.data.confirmationContent() : "" }
                    <div className="buttons">
                        <Button label="Hủy" isWeak={true} onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hide : null}/>
                        <Button label="Xác nhận đặt hàng" onClick={typeof(props.handlers)!=="undefined" ? props.handlers.showOrderSuccess : null}/>
                    </div>
                </div>
            </div>
        </div>
    );
}

const OrderSuccessContent = (props)=>{
    return(
        <div className="feedback">
            <h3 style={{textAlign:"center"}}>Đặt hàng thành công</h3>
            <p style={{textAlign:"center"}}>Đơn hàng của bạn sẽ được ban kiểm duyệt nhà trường xử lý. Bạn có thể hủy đơn hàng trước khi đơn hàng được duyệt. Nếu có vấn đề cần giải quyết, vui lòng sử dụng tính năng báo cáo để gửi báo cáo đến nhà trường.</p>
            <div className="buttons">
                <Button label="Đóng" onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hide : null}/>
            </div>
        </div>
    );
}

const PrintOrderSuccessContent = (props)=>{
    return(
        <div className="feedback">
            <h3 style={{textAlign:"center"}}>Đăng ký thành công</h3>
            <p style={{textAlign:"center"}}>Bạn đã đăng ký in thành công! Bạn vui lòng đợi nhận tài liệu sau thứ 4, thứ 6 hàng tuần</p>
            <div className="buttons">
                <Button label="Tiếp tục mua hàng" onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hide : null}/>
            </div>
        </div>
    );
}

const CreateCategory = (props)=>{
    const [ncc, setNcc] = useState("");
    const [name, setName] = useState("");
    const [description, setDescription] = useState("");
    const [active,setActive] = useState(true);

    const activeCat = ()=>{
        setActive(true);
    }

    const unActiveCat = ()=>{
        setActive(false);
    }

    const createCategory = ()=>{
        if(typeof(props.handlers)!=="undefined"){
            props.handlers.createCategory({name: name, provider: ncc, description: description, status: active}, props.id);
            setTimeout(()=>{
                setNcc("");
                setName("");
                setDescription("");
                setActive(true);
            },500);  
        } 
    }

    const renderAddButton = ()=>{
        if (typeof(props.handlers) !== "undefined"){
            if((typeof(props.handlers.updateFlag) !== "undefined") && (props.handlers.updateFlag === 1)){
                return(<Button label={"Cập nhật"}  style={{margin: "25px 15px"}}  onClick={createCategory}/>);
            }else{
                return(<Button label={"Thêm"}  style={{margin: "25px 15px"}}  onClick={createCategory}/>);
            }
        }else{
            return(<Button label={"Thêm"}  style={{margin: "25px 15px"}}  onClick={createCategory}/>);
        }
    }

    useEffect(()=>{
        if(props.data){
            let data = JSON.parse(props.data);
            setNcc(data.provider);
            setName(data.name);
            setDescription(data.description);
            setActive(data.show);
        } 
    },[props.data]);

    return(
        <div className="create-category">
            <h3>Thêm danh mục</h3>
            <label>Tên danh mục</label>
            <input type="text" value={name} onChange={(event)=>{setName(event.target.value);}} placeholder = "Nhập tên danh mục tại đây" />
            <label>Mô tả</label>
            <textarea value={description} onChange={(event)=>{setDescription(event.target.value);}} placeholder = "Nhập mô tả tại đây" />
            <label>Nhà cung cấp</label>
            <input type="text" value={ncc} onChange={(event)=>{setNcc(event.target.value);}} placeholder = "Nhập tên nhà cung cấp tại đây" />
            <div className="selection-group">
                <label>Trạng thái</label>
                <div className="option">
                    <RadioButton label="Hiển thị" selected={active === true ? 1 : 0} onClick={()=>{activeCat();}}/>
                    <RadioButton label="Ẩn" selected={active === true ? 0 : 1} onClick={()=>{unActiveCat();}}/>
                </div>
            </div>
            <div className="button-group">
                <Button label="Quay lại" isWeak={true} style={{margin: "25px 15px"}} onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hideCreateCategoryPopup : null}/>
                {renderAddButton()}
            </div>
        </div>
    );
}

const CreateProduct = (props)=>{
    const [name, setName] = useState("");
    const [description, setDescription] = useState("");
    const [price, setPrice] = useState("");
    const [productCode, setProductCode] = useState("");
    const [category,setCategory] = useState("");
    const [active,setActive] = useState(true);
    const [thumbnail,setThumbnail] = useState("");
    const [file, setFile] = useState(null);
    const [loading, setLoading] = useState(0);

    const activeProd = ()=>{
        setActive(true);
    }

    const unActiveProd = ()=>{
        setActive(false);
    }

    const createProduct = ()=>{
        if(typeof(props.handlers)!=="undefined"){
            setLoading(1);
            props.handlers.createProduct({
                name: name, 
                description: description, 
                status: active, 
                price: price, 
                code: productCode,
                category:category  ,
                thumbnail: thumbnail,
                file: file
            },()=>{setLoading(0);});
            setTimeout(()=>{
                setCategory("");
                setName("");
                setDescription("");
                setPrice("");
                setProductCode("");
                setFile(null);
                setActive(true);
            },500);  
        } 
    }

    const renderAddButton = ()=>{
        if (typeof(props.handlers) !== "undefined"){
            if((typeof(props.handlers.updateFlag) !== "undefined") && (props.handlers.updateFlag === 1)){
                return(<Button label={"Cập nhật"}  style={{margin: "25px 15px"}}  onClick={createProduct}/>);
            }else{
                return(<Button label={"Thêm"}  style={{margin: "25px 15px"}}  onClick={createProduct}/>);
            }
        }else{
            return(<Button label={"Thêm"}  style={{margin: "25px 15px"}}  onClick={createProduct}/>);
        }
    }

    useEffect(()=>{
        if(props.data){
            let data = JSON.parse(props.data);
            setName(data.name);
            setDescription(data.description);
            setPrice(data.price);
            setProductCode(data.code);
            setCategory(data.category);
            setActive(data.show);
            setThumbnail(data.thumbnail)
        } 
    },[props.data]);

    return(
        <div className="create-category create-product" style={{opacity: loading === 1 ? 0.3 : 1 }}>
            <h3>Thêm sản phẩm</h3>
            <div className="row">
                <div className="col" style={{paddingRight: '15px'}}>
                    <label>Thuộc danh mục</label>
                   <Dropdown  placeholder="Chọn danh mục" options={typeof(props.dropdownData) !== "undefined" ? typeof(props.dropdownData.category) !== "undefined" ? props.dropdownData.category :[] : []}  onClick = {(value)=>{setCategory(value);}}></Dropdown>
                </div>
                <div className="col">
                    <label>Mã sản phẩm</label>
                    <input type="text" value={productCode} onChange={(event)=>{setProductCode(event.target.value);}} placeholder = "Nhập mã sản phẩm" />
                </div>
            </div>
            <div className="row">
                <div className="col" style={{paddingRight: '15px'}}>
                    <label>Tên sản phẩm</label>
                    <input type="text" value={name} onChange={(event)=>{setName(event.target.value);}} placeholder = "Nhập tên sản phẩm" />
                </div>
                <div className="col">
                    <label>Giá tiền</label>
                    <input type="text" value={price} onChange={(event)=>{setPrice(event.target.value);}} placeholder = "Nhập giá tiền" />
                </div>
            </div>
            <div className="row">
                <div className="col" style={{paddingRight: '15px'}}>
                    <label>Hình ảnh</label>
                    <input type="file" accept="image/*" onChange={(event)=>{setFile(event.target.files[0])}}/>
                    <div className="selection-group">
                        <label>Trạng thái</label>
                        <div className="option">
                            <RadioButton label="Hiển thị" selected={active === true ? 1 : 0} onClick={()=>{activeProd();}}/>
                            <RadioButton label="Ẩn" selected={active === true ? 0 : 1} onClick={()=>{unActiveProd();}}/>
                        </div>
                    </div>
                </div>
                <div className="col">
                    <label>Mô tả</label>
                    <textarea value={description} onChange={(event)=>{setDescription(event.target.value);}} placeholder = "Nhập mô tả tại đây" />
                </div>
            </div>
            
           
            <div className="button-group">
                <Button label="Quay lại" isWeak={true} style={{margin: "25px 15px"}} onClick={typeof(props.handlers)!=="undefined" ? props.handlers.hideCreateCategoryPopup : null}/>
                {renderAddButton()}
            </div>
        </div>
    );
}
const Popup = (props)=>{

    const [show,setShow] = useState(props.show);
    const [popupName, setPopupName] = useState(props.name);

    useEffect(()=>{
        setPopupName(props.name);
    },[props.name])

    const showContent = ()=>{
        switch (popupName){
            case "feedback": 
                return <FeedbackContent 
                    orderID = {props.data.orderID} 
                    orderType = {props.data.orderType} 
                    orderDate={props.data.orderDate}
                    handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null}
                />;
            case "feedback-success":
                return <FeedbackSuccessContent  handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null}/>   
            case "cancel-order":
                return <CancelContent id={typeof(props.data) !== "undefined" ? props.data.id : null} handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null}/>
            case "confirmation":
                return <ConfirmationContent handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null} data={typeof(props.data) !== "undefined" ? props.data : null}/>
            case "order-success":
                return <OrderSuccessContent handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null}/>
            case "print-order-success":
                return <PrintOrderSuccessContent handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null}/>
            case "create-category":
                 return <CreateCategory handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null} data={typeof(props.data) !== "undefined" ? props.data : null}/>
            case "create-product":
                return <CreateProduct handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null} data={typeof(props.data) !== "undefined" ? props.data : null} dropdownData={props.dropdownData}/>        
            
            default:
                return null;
        }
    }

    useEffect(()=>{
        setShow(props.show);
    },[props.show]);

    return (
        <div className={show === 1 ? "popup show" : "popup"}>
            <div className="wrapper">
                <div className="overlay"></div>
                <div className="container">
                    {
                      showContent()  
                    }
                </div>
            </div>
        </div>
    );
}

export default Popup;