import {useEffect, useRef, useState } from 'react';
import CartContext from '../Contexts/Cart';
import {GetCartID}  from '../components/ultility';
import Header from "../components/header"
import Footer from "../components/footer"
import Home from "../views/home"
import Printing from "../views/printing"
import Cart from "../views/cart"
import History from "../views/history"
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
  

const footerData = {
    schoolData:{
      logo: "https://tuyensinhthptcantho.fpt.edu.vn/uploads/file/2020/03/30/logo_header.png",
      name: "TRƯỜNG THPT FPT CẦN THƠ",
      hotline: "+84907823684",
      email: "support@fpt.edu.vn",
      address: "Số 600, Khu vực 6, đường Nguyễn Văn Cừ (nối dài), Phường An Bình, Quận Ninh Kiều, Thành phố Cần Thơ"
    },
    aboutData : [
      {text: "Giới thiệu trường THPT FPT Cần Thơ", url:"/gioi-thieu"},
      {text: "Giới thiệu Dino shop", url:"/gioi-thieu-dino-shop"},
      {text: "Quy định mua hàng", url:"/quy-dinh-mua-hang"},
    ],
    productData : [
      {text: "Văn phòng phẩm", url:"/van-phong-pham"},
      {text: "Nhu yếu phẩm", url:"/nhu-yeu-pham"}
    ]
  }

  
const menuItem = [
    <Link to="/" className="link" key="1">Trang chủ</Link>,
    <Link to="/" className="link" key="2">Văn phòng phẩm</Link>,
    <Link to="/" className="link" key="3">Nhu yếu phẩm</Link>,
    <Link to="/in-tai-lieu" className="link" key="4">In tài liệu</Link>,
];


const Student = ()=>{
    const [currentQuantity,setCurrentQuantity] = useState(0);
    const [update,setUpdate] = useState(0);
    let _cartData = useRef([
        {
        title: "Văn phòng phẩm",
        items:[]
        },
        {
        title: "Nhu yếu phẩm",
        items:[]
        }
    ]);

    // data and function for review order
    let _orderReviewData = useRef(null);
    let _orderReviewMode = useRef(0);

    let selectedItemsQuantity = useRef({});

    const addDataForReviewCart = (type,_data)=>{
        let data = {
        title:type,
        items: JSON.parse(_data)
        }
        _orderReviewData.current = data;
        setUpdate(update + 1);
    }

    const switchCartMode = (status,manualUpdate = false)=>{
        _orderReviewMode.current = status;
        if(manualUpdate === true){
        setUpdate(update + 1);
        }
        
    }

    const debugOrderReview = ()=>{
        console.log(_orderReviewMode.current);
        console.log(_orderReviewData.current);
    }

    console.log("app-re-render");

    // ----

    // get current total quantity in cart
    const getCartQuantity = (callback)=>{
        if(callback){callback(currentQuantity)};
        return currentQuantity;
    }

    // Thêm item vào list giỏ hàng
    const addToCart = (_quantity,_section,_productInfo)=>{
        let itemNum = _cartData.current[_section].items.length;
        setCurrentQuantity(_quantity);
        if(itemNum === 0){
        _productInfo.index = _section +"-0";
        }else{
        _productInfo.index = _section +"-"+(itemNum);
        }
        _productInfo.section = _section;
        _cartData.current[_section].items.push(_productInfo);
        return _productInfo.index;
    }

    // update số lượng có trong list giỏ hàng
    const updateItemQuantityInCart = (itemPosition, quantity)=>{
        const index = GetCartID(itemPosition);
        _cartData.current[index[0]].items[index[1]].quantity = quantity;
    }
    
    // update số lượng cho các item đã add vào giỏ -> mục đích back lại sẽ thấy auto fill
    const updateQuantityData = (productID, _quantity, _index, _section)=>{
        
        if(typeof(selectedItemsQuantity.current[_section]) === "undefined"){
        selectedItemsQuantity.current[_section] = {};
        }  
    
        selectedItemsQuantity.current[_section][productID] = {quantity : _quantity, index: _index };
    } 

    const getQuantityList = ()=>{
        return selectedItemsQuantity.current;
    }
    
    console.log(_orderReviewData.current);

    return(
        <Router>
            <CartContext.Provider value={{
                addToCartHandler: addToCart, 
                updateCartHandler: updateItemQuantityInCart,
                getCartQuantityHandler: getCartQuantity, 
                cartData: _cartData.current, 
                orderReviewData: _orderReviewData.current,
                orderReviewMode: _orderReviewMode.current,
                addDataForReviewCartHandler: addDataForReviewCart,
                switchCartModeHandler:switchCartMode,  
                debugOrderReviewData: debugOrderReview,
                updateQuantityListHandler: updateQuantityData, 
                getQuantityListHandler: getQuantityList 
            }}>
                <Header includeCart = {true} menuItems={menuItem}></Header>
                <Switch>
                    <Route exact path="/">
                        <Home />
                    </Route>
                    <Route exact path="/in-tai-lieu">
                        <Printing />
                    </Route>
                    <Route exact path="/gio-hang">
                        <Cart />
                    </Route>
                    <Route exact path="/lich-su-dat-hang">
                        <History />
                    </Route>
                </Switch>
                <Footer info = {footerData.schoolData} aboutData={footerData.aboutData} productData={footerData.productData}></Footer>
            
            </CartContext.Provider>
        </Router>
    );
}

export default Student;