import { Link } from "react-router-dom";
import CartContext from "../Contexts/Cart";
import './header.css';
import CartIcon from '../images/cart.svg'

const UserInfo = (props)=>{

    const renderCartShortcut = ()=>{
        if(props.includeCart === true){
            return(
                <CartContext.Consumer>
                    {
                        context => (
                            <Link to="/gio-hang" className="link">
                                <div className="cart" onClick = {()=>{context.switchCartModeHandler(0,true);}}>
                                    <img src={CartIcon} alt="giỏ hàng"/>
                                    <div className="counter">
                                        {
                                            context.getCartQuantityHandler(
                                                quantity =>{
                                                    if((quantity > 0)){
                                                        const counter = document.querySelector(".counter");
                                                        counter.className = "counter updating";
                                                        const timeout = setTimeout(()=>{
                                                            counter.className = "counter";
                                                            clearTimeout(timeout);
                                                        },600);
                                                    }
                                                }
                                            )
                                        }
                                    </div>
                                </div>
                            </Link>
                        )
                    }
                </CartContext.Consumer>
            );
        }
    }

    return(
        <div className="user-info">
            <div className="info">
                <p>Chào bạn</p>
                <p>{props.userName}</p>
            </div>
            {renderCartShortcut()}
            <div class="sub-menu">
                <Link to="/lich-su-dat-hang" className="link">Lịch sử đặt hàng</Link>
                <Link to="" className="link">Đăng xuất</Link>      
            </div>    
        </div>     
    );
}


const Header = (props) => {
    return(
        <div className="header-container">
            <div className="header">
                <div className="container">
                    {props.menuItems}

                    <UserInfo userName="Minh Hùng" includeCart = {props.includeCart}></UserInfo>
                </div>
            </div>
        </div>
    )
}

export default Header;