import { useEffect, useRef, useState  } from "react";
import {createCategory, getCategoryCaching, getProviderCaching} from '../models/data'
import Button from '../components/button';
import Popup from '../components/popup';
import './admin.css'
import Dropdown from "../components/dropdown";
import Paginator from "../components/paginator";

const TableRow= (props)=>{
    let elem = [];
    if(typeof(props.data) !== "undefined"){
        props.data.forEach((item,index) => {
            elem.push(<div className="table-col" style={{flex: props.size[index], fontWeight: typeof(props.header) !== "undefined"? 600 : 500}}>{item}</div>);
        });
    }

    return(
        <div className="table-row">
            {elem}
        </div>
    )
}


const AdminCategory = () => {
    const [showPopup, setShowPopup] = useState(0);
    const [popupName, setPopupName] = useState("create-category");
    const [catData, setCatData] = useState([]);
    const [providerData,setProviderData] = useState([]);
    const [catInfo, setCatInfo] = useState(null);
    const [renderCount,setRenderCount] = useState(0);
    const [selectedProvider, setSelectedProvider] = useState("");
    const [selectedStatus, setSelectedStatus] = useState("")
    const update = useRef(0);
    const currentCatID = useRef(null);

    let categoryRow = [];

    const showCreateCategoryPopup = ()=>{
        setShowPopup(1);
    }

    const hideCreateCategoryPopupHandler = ()=>{
        setShowPopup(0);
        setCatInfo(null);
    }

    const switchToUpdateModeHandler = ()=>{
        update.current = 1;
    }

    const createCategoryHandler = async (data)=>{
        let tmp = catData;
        let date = new Date;
        let providers = [];
        providers = await createCategory(data,update.current === 1? "u" : "w",currentCatID.current);
        if(update.current !== 1){
            tmp.push({
                id: currentCatID.current,
                name: data.name,
                provider: data.provider,
                description: data.description,
                createDate: date.getDate() +"/"+ (date.getMonth() +1) +"/"+ date.getFullYear(),
                show: data.status
            });
            setCatData(tmp);
        }else{
            for(let i = 0; i < tmp.length; i++){
                if(tmp[i].name === data.name){
                    tmp[i] = {
                        id: currentCatID.current,
                        name: data.name,
                        provider: data.provider,
                        description: data.description,
                        createDate: date.getDate() +"/"+ (date.getMonth() +1) +"/"+ date.getFullYear(),
                        show: data.status
                    }
                }
            }
            setCatData(tmp);
        }
        currentCatID.current = null;
        update.current = 0;
        setShowPopup(0);
        setProviderData(providers['filter']);
    }

    const bindCatDataHandler = (data)=>{
        setCatInfo(JSON.stringify(data));
        setShowPopup(1);
    }

    useEffect(async ()=>{
        if(renderCount === 0){
            const data = await getCategoryCaching();
            const _providers = await getProviderCaching();
            console.log(_providers);
            setProviderData(_providers['filter']);
            setCatData(data);
            setRenderCount(1);
        }
    },[renderCount]);

    if(catData.length > 0){
        catData.forEach((cat,index)=>{
            if(selectedProvider !== ""){
                if(cat.provider === selectedProvider){
                    categoryRow.push(<TableRow data={[index,cat.name,cat.description, cat.provider,cat.show === true ? "Hiển thị" : "Đang ẩn", cat.createDate,[
                        <Button label="sửa" onClick={()=>{
                            bindCatDataHandler({
                                id: cat.id,
                                name: cat.name,
                                description: cat.description,
                                provider: cat.provider,
                                show: cat.show
                            });
                            console.log(cat.id);
                            update.current = 1;
                            currentCatID.current = cat.id;
                        }}/>, 
                        <Button label="xóa" isWeak={true}/>
                    ]]} size={[1,5,9,4,2,2,5]}/>);
                }
            }else{
                if(selectedStatus !== ""){
                    if((selectedStatus === true)&&(cat.show === true)){
                        categoryRow.push(<TableRow data={[index,cat.name,cat.description, cat.provider,cat.show === true ? "Hiển thị" : "Đang ẩn", cat.createDate,[
                            <Button label="sửa" onClick={()=>{
                                bindCatDataHandler({
                                    id: cat.id,
                                    name: cat.name,
                                    description: cat.description,
                                    provider: cat.provider,
                                    show: cat.show
                                });
                                console.log(cat.id);
                                update.current = 1;
                                currentCatID.current = cat.id;
                            }}/>, 
                            <Button label="xóa" isWeak={true}/>
                        ]]} size={[1,5,9,4,2,2,5]}/>);
                    }else if((selectedStatus === false)&&(cat.show !== true)){
                        categoryRow.push(<TableRow data={[index,cat.name,cat.description, cat.provider,cat.show === true ? "Hiển thị" : "Đang ẩn", cat.createDate,[
                            <Button label="sửa" onClick={()=>{
                                bindCatDataHandler({
                                    id: cat.id,
                                    name: cat.name,
                                    description: cat.description,
                                    provider: cat.provider,
                                    show: cat.show
                                });
                                console.log(cat.id);
                                update.current = 1;
                                currentCatID.current = cat.id;
                            }}/>, 
                            <Button label="xóa" isWeak={true}/>
                        ]]} size={[1,5,9,4,2,2,5]}/>);
                    }
                }else{
                    categoryRow.push(<TableRow data={[index,cat.name,cat.description, cat.provider,cat.show === true ? "Hiển thị" : "Đang ẩn", cat.createDate,[
                        <Button label="sửa" onClick={()=>{
                            bindCatDataHandler({
                                id: cat.id,
                                name: cat.name,
                                description: cat.description,
                                provider: cat.provider,
                                show: cat.show
                            });
                            console.log(cat.id);
                            update.current = 1;
                            currentCatID.current = cat.id;
                        }}/>, 
                        <Button label="xóa" isWeak={true}/>
                    ]]} size={[1,5,9,4,2,2,5]}/>);
                }
            }
        });
    }

    return (
        <div className="admin-view">
            <div className="header">
                <div className="title">
                    <h3>Quản lý sản phẩm / danh mục</h3>
                </div>
                <div className="button-group">
                    <Button label="Thêm danh mục" onClick = {showCreateCategoryPopup}></Button>
                </div>
            </div>
            <div className="table">
                <div className="table-header">
                   <div className="search"></div>
                   <div className="filter-group">
                       <Dropdown placeholder="Nhà cung cấp" options={providerData} onClick = {(value)=>{setSelectedProvider(value);}}/>
                       <Dropdown placeholder="Trạng thái" options={[{text:"Tất cả trạng thái", value:true},{text:"Hiển thị", value:true},{text:"Ẩn", value:false}]} onClick = {(value)=>{setSelectedStatus(value);}}/>
                   </div>
                </div>
                <div className="table-data">
                    <TableRow data={["STT","Tên danh mục","Mô tả", "Nhà cung cấp","Trạng thái", "Ngày CN",""]} size={[1,5,9,4,2,2,5]} header={true}/>
                    <hr/>
                    <div className="table-content">
                        {categoryRow}
                    </div>
                    <Paginator total={3}/>
                </div>
            </div>
            <Popup
                name={popupName} 
                show={showPopup}
                data={catInfo}
                handlers= {{
                    hideCreateCategoryPopup: hideCreateCategoryPopupHandler, 
                    createCategory: createCategoryHandler,
                    switchToUpdateMode: switchToUpdateModeHandler,
                    updateFlag : update.current,
                }}
            ></Popup>
        </div>
    );
}

export default AdminCategory;