import React,{useEffect, useRef, useState } from "react"
import CartContext from "../Contexts/Cart";
import Button from "./button";
import './cart-list.css';
import QuantityButton from "./quantity-button";
import Checkbox from "./checkbox";
import FormatNumber,{GetCartID} from "./ultility";
import RemoveAllIcon from '../images/remove-all.svg'

const CartBindedData = React.createContext();

const RemoveAllButton = (props) =>{
    
    const [show,setShow] = useState(props.show);
    // const [selectedItems,setSelectedItems] = useState(props.selectedItems);
    
    useEffect(()=>{
        setShow(props.show);
    },[props.show,props.selectedItems]);


    return(
        <CartBindedData.Consumer>
            {
                context => (
                    <div className="remove-cart-section" style={{display: show === 1 ? "block" : "none"}}
                        onClick={()=>{
                            if(typeof(context) !== "undefined"){
                                let _tmp = context.storedData;
                                if(props.selectedItems){
                                    for(let i = 0 ; i < props.selectedItems.length; i++){
                                        const index = GetCartID(props.selectedItems[i]);
                                        _tmp[index[0]].items[index[1]] = "";
                                    }
                                }else{
                                    _tmp[props.sectionID] = "";
                                }
                                context.updateDataHandler(JSON.stringify(_tmp));
                            }
                        }}
                    > 
                        <img src={RemoveAllIcon }  alt="xóa hết"/>
                    </div>
                )
            }
        </CartBindedData.Consumer>
       
    )
}

const CartItem = (props)=>{
    return(
        <CartContext.Consumer>
            {
                _context => (
                    <CartBindedData.Consumer>
                    {
                        context => (
                            <div className="item row">
                                <div className="col"><Checkbox itemID={props.itemID} checked={props.selected} onClick = {props.onSelectHandler}/></div>
                                <div className="col col-2">
                                    <div className="thumbnail">
                                        <img src={props.thumbnail} alt="hình sản phẩm"/>
                                    </div>
                                    <div className="name-description">
                                        <h3>{props.name}</h3>
                                        <p>{props.description}</p>
                                    </div>
                                    <div className="price">
                                        {FormatNumber(props.price)}đ
                                    </div>
                                </div>
                                <div className="col">
                                    <QuantityButton 
                                        quantity= {props.quantity} 
                                        addedToCart={props.addedToCart} 
                                        cartData = {context} 
                                        itemID = {props.itemID}
                                        isCartView = {true}
                                        mode={props.mode}
                                        productInfo ={{
                                            index: typeof(_context.getQuantityListHandler()[props.sectionID]) !== "undefined" ? (typeof(_context.getQuantityListHandler()[props.sectionID][props.id]) !== "undefined" ? _context.getQuantityListHandler()[props.sectionID][props.id].index : null) : null,
                                            id: props.id,
                                            name: props.name,
                                            thumbnail: props.thumbnail,
                                            price: props.price,
                                            description: props.description
                                        }}
                                    />
                                </div>
                            </div>
                        )
                    }
                    
                </CartBindedData.Consumer>
                )
            }
        </CartContext.Consumer>
    );
}

const TotalInfo = (props)=>{

    const renderButton = ()=>{
        if(props.mode === "order-review"){
            return null;
        }else{
            return(
                <Button label="Đăng kí mua hàng" style={{marginLeft: 0}} onClick = {()=>{
                    if(typeof(props.handlers)!=="undefined"){
                        props.handlers.showConfirmation({
                            content:props.data,
                            total: props.total,
                            section: props.sectionTitle
                        });
                    }  
                }}></Button>
            );
        }
    }


    return(
        <div className="cart-section-total-info">
            <h3>Thông tin đơn hàng</h3>
            <hr/>
            <p>Tạm tính đơn {props.sectionTitle}</p>
            <p><span>Tổng:</span> {FormatNumber(props.total)}đ</p>
            <p> ({props.itemCount} sản phẩm)</p>
            <div className="note">
                <p>Lưu ý khi đặt hàng:</p>
                <ul>
                    <li>Đặt hàng không được huỷ</li>
                    <li>Bom hàng sẽ bị xử lý theo quy định</li>
                </ul>
            </div>
            {renderButton()}    
        </div>
    );
}



const CartSection = (props)=>{
    const [selectAll,setSelectAll] = useState(0);
    const [selectedItems, setSelectedItems] = useState(null);
    let _selectedItems = useRef([]); //modify on this var
    let itemList = []; 
    let total = 0;

    const addSelectedItemToList = (itemID)=>{
        _selectedItems.current.push(itemID);
        setSelectedItems(_selectedItems.current);
        return selectedItems;
    }

    const removeSelectedItemFromList = (itemID)=>{
        const index = _selectedItems.current.indexOf(itemID);
        if (index > -1) {
            _selectedItems.current.splice(index, 1);
        }
        setSelectedItems(_selectedItems.current);
        return selectedItems;
    }

    if(props.items){
        props.items.forEach(item => {
            if(item){
                itemList.push(
                    <CartItem 
                        key={item.index} 
                        itemID= {item.index} 
                        id= {item.id}
                        thumbnail={item.thumbnail} 
                        name={item.name} 
                        description={item.description} 
                        price={item.price} 
                        quantity={item.quantity} 
                        addedToCart={1} 
                        selected={selectAll}
                        sectionID={item.section}
                        onSelectHandler = {{addSelectedItem: addSelectedItemToList, removeSelectedItem: removeSelectedItemFromList}}
                        mode={props.mode}
                    />);
                total += (item.price * item.quantity);
            }
            
        });
        
    }
    return (
        <div className="cart-section">
            <div className="list">
                <div className="header row">
                    <div className="col">
                        <Checkbox onClick={(status)=>{
                            setSelectAll(status);
                        }}/>
                    </div>
                    <div className="col col-2"><h3>{props.title}</h3></div>
                    <div className="col" style={{paddingTop: "1.7em"}}>
                        <RemoveAllButton 
                            show={selectedItems !== null ? (selectedItems.length > 0 ? 1 : 0) : (selectAll === 1 ? 1 : 0)} 
                            sectionID = {props.index} 
                            selectedItems={selectedItems}
                        />
                    </div>
                </div>
                <hr/>
                {itemList}
            </div>
            <TotalInfo data={props.items} sectionTitle={props.title} itemCount = {props.items.length} total={total} mode={props.mode} handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null}></TotalInfo>
        </div>
    );
}


const CartList = (props)=>{
    const [data, setData] = useState(props.data);
    let cartSection = [];

    const updateData = (_data)=>{
        setData(JSON.parse(_data));
    }

    useEffect(()=>{
        setData(props.data);
    },[props.data])
    
    data.forEach((_data,index) => {
        if(_data.items.length > 0){
            cartSection.push(<CartSection key={index} index={index} title={_data.title} items = {_data.items} mode={props.mode} handlers = {typeof(props.handlers)!=="undefined" ? props.handlers : null}/>);
        }
    });

    return(
        <CartBindedData.Provider  value={{updateDataHandler: updateData, storedData: data}}>
            <div className="cart-list">
                <div className="container">
                    {cartSection}
                </div>
            </div>
        </CartBindedData.Provider>
    );
}

export default CartList;