import {useState } from "react"
import { Link } from "react-router-dom"
import CartContext from "../Contexts/Cart"
import './product-list.css'
import QuantityButton from './quantity-button'
import FormatNumber from "./ultility";


const ProductItem = (props)=>{

    const [isAddedToCart, setIsAddedToCart] = useState(0);
    const [quantity, setQuantity] = useState(0);

    const updateQuantity = (_quantity, _isAddedToCart)=>{
        setQuantity(_quantity);
        setIsAddedToCart(_isAddedToCart);
    }

    return(
        <CartContext.Consumer>
            {
                context =>(
                    <div className="product-item-container">
                        <div className="product-item">
                            <div className = "product-thumbnail">
                                <img src={props.thumbnail} alt={props.name}/>
                                <div className ="product-notify" style={{padding: isAddedToCart === 1 ? "8px" : "0px"}}>{isAddedToCart === 1 ? "Đã thêm " + quantity + " sản phẩm" : ""}</div>
                            </div>
                            <h3 className="product-name">{props.name}</h3>
                            <p  className="product-description">{props.description}</p>
                            <h4 className="product-price">{FormatNumber(props.price)}đ</h4>
                            <QuantityButton 
                                quantity = {typeof(context.getQuantityListHandler()[props.sectionID]) !== "undefined" ? (typeof(context.getQuantityListHandler()[props.sectionID][props.id]) !== "undefined" ? context.getQuantityListHandler()[props.sectionID][props.id].quantity : 0) : 0}
                                addedToCart = {typeof(context.getQuantityListHandler()[props.sectionID]) !== "undefined" ? (typeof(context.getQuantityListHandler()[props.sectionID][props.id]) !== "undefined" ?(context.getQuantityListHandler()[props.sectionID][props.id].quantity > 0 ? 1 : 0) : 0) : 0}
                                updateHandler = {updateQuantity} 
                                sectionID={props.sectionID}
                                isCartView = {false} 
                                productInfo ={{
                                    index: typeof(context.getQuantityListHandler()[props.sectionID]) !== "undefined" ? (typeof(context.getQuantityListHandler()[props.sectionID][props.id]) !== "undefined" ? context.getQuantityListHandler()[props.sectionID][props.id].index : null) : null,
                                    id: props.id,
                                    name: props.name,
                                    thumbnail: props.thumbnail,
                                    price: props.price,
                                    description: props.description
                                }}
                            ></QuantityButton>
                        </div>
                    </div>
                )
            }
        </CartContext.Consumer>
    );
}



const ProductList = (props)=>{
    let list = [];

    for (let i = 0; i < props.productList.length; i++) {
        const name = props.productList[i].name;
        const price = props.productList[i].price;
        const description = props.productList[i].description;
        const thumbnail = props.productList[i].thumbnail;
        const id = props.productList[i].id;
        if(props.limit){
            if(i < props.limit){
                list.push(<ProductItem key={i} id={id} name={name} price={price} description={description} thumbnail={thumbnail} sectionID={props.sectionID}/>);
            }else{
                break;
            }
        }else{
            list.push(<ProductItem key={i} id={id} name={name} price={price} description={description} thumbnail={thumbnail} sectionID={props.sectionID}/>);
        }   
    }

    return(
        <div className="product-list-container">
            <h2>{props.title}</h2>
            <div className="product-list">
                {list}
            </div>
            <Link to={props.route} className="link">Xem tất cả</Link>
            <hr></hr>
        </div>
    )

}

export default ProductList;